@import '../../../../variables/variables.scss';

.weather-app-page {
	width: 100%;
	height: 100%;
	min-height: 100vh;
	background-color: var(--bg);
}

.weather-app-page__main {
	width: 100%;
	height: 100%;
	min-height: 100vh;
	padding: 50px 0 var(--padding-for-menu) 0;

	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	gap: 30px;

	@media (max-width: $mobile-width) {
		padding: 0 0 var(--padding-for-menu) 0;
		gap: 20px;
	}
}